// react
import React, { Component } from 'react';

// react-bootstrap
import {
  Button,
  Clearfix,
  Col,
  Label,
  Modal,
  Panel,
} from 'react-bootstrap';

// custom
import {
  tunnelBgs,
} from '../lib/data';

class Memo extends Component {
  render() {
    const { memo } = this.props;
    const { type, valueThresholds: vt, skillValueBonus: vb, skills: s, name, reward } = memo;

    return (
      <Col
        className='memo'
        xs={6}
      >
        <div>{name}</div>
        <div>
          <span>{type}</span>
          {vt && <span>{vt.map(a => a.join(':')).join(',')}</span>}
          {vb && <span>{`${vb[0]} ${vb[1]}的 +${vb[2]}`}</span>}
          {s && <span>{`某隻擁有 ${s}`}</span>}
        </div>
        <div>{`${reward[0]} * ${reward[1]}`}</div>
      </Col>
    );
  }
}

export class Tunnel extends Component {
  render() {
    const { tunnel, isSelected, showSub, onSelect, onOpenModal } = this.props;
    const { key, name, count, memos } = tunnel;

    const className = 'tunnel' + (isSelected ? ' selected' : '');

    return (
      <Panel className={className}>
        <Panel.Heading
          style={{ backgroundImage: `url(${tunnelBgs[key]})` }}
        >
          <Label>{name}</Label>
          {/*!showSub && <div className='spacer'></div>*/}
          {!showSub && <Button onClick={() => onOpenModal()}>選擇洞穴</Button>}
          {showSub && <Button onClick={() => onSelect(tunnel)} disabled={isSelected}>選擇</Button>}
          {/*showSub && <div className='spacer'></div>*/}
          {showSub && <Panel.Toggle componentClass='a'>檢視內容</Panel.Toggle>}
        </Panel.Heading>
        {showSub && <Panel.Collapse>
          <Panel.Body>
            <div>
              <div>{'菇菇數量: ' + (count || 3)}</div>
              <div>
                {(() => {
                  const comps = [];

                  memos.forEach((memo, idx) => {
                    comps.push(<Memo key={idx} memo={memo} />);

                    if (idx % 2 === 1) {
                      comps.push(<Clearfix key={`_${idx}`} visibleXsBlock />);
                    }
                  });

                  return comps;
                })()}
              </div>
            </div>
          </Panel.Body>
        </Panel.Collapse>}
      </Panel>
    );
  }
}

export default class extends Component {
  render() {
    const { show, showModal, allTunnels, selectedTunnel, onSelectTunnel } = this.props;

    return (
      <Modal
        className='tunnel-modal'
        show={show}
        onHide={() => showModal(false)}
      >
        <Modal.Header>
          <Modal.Title>選擇洞穴</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {allTunnels.map((tunnel, idx) => (
            <Tunnel
              key={idx}
              tunnel={tunnel}
              showSub={true}
              isSelected={tunnel === selectedTunnel}
              onSelect={tunnel => {
                showModal(false);
                onSelectTunnel(tunnel);
              }}
            />
          ))}
        </Modal.Body>
        <Modal.Footer>
          <Button
            bsSize='large'
            bsStyle='primary'
            onClick={() => showModal(false)}
          >
            關閉
          </Button>
        </Modal.Footer>
      </Modal>
    )
  }
}
