// react
import React, { Component } from 'react';

// react-bootstrap
import {
  Button,
  Clearfix,
  Modal,
} from 'react-bootstrap';

// custom
import Nameko from './Nameko';

export default class extends Component {
  render() {
    const { show, showModal, levels, allNamekos, selectedNamekos, onClickNameko, onClickLevel } = this.props;

    return (
      <Modal
        className='nameko-list-modal'
        show={show}
        onHide={() => showModal(false)}
      >
        <Modal.Header>
          <Modal.Title>選擇菇菇</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div>
            {levels.map((level, idx) => (
              <Button
                key={idx}
                onClick={() => onClickLevel(level)}
              >
                {level.name}
              </Button>
            ))}
          </div>
          <div>
            <Button onClick={() => {
              allNamekos.forEach(n => selectedNamekos.includes(n) || onClickNameko(n));
            }}>
              全選
            </Button>
            <Button onClick={() => {
              allNamekos.forEach(n => selectedNamekos.includes(n) && onClickNameko(n));
            }}>
              全不選
            </Button>
          </div>
          <div className='all-block'>
            {(() => {
              const comps = [];

              allNamekos.forEach((nameko, idx) => {
                comps.push(<Nameko
                  key={idx}
                  nameko={nameko}
                  isSelected={selectedNamekos.includes(nameko)}
                  onClick={() => onClickNameko(nameko)}
                />);

                if (idx % 3 === 2) {
                  comps.push(<Clearfix key={`_${idx}`} visibleXsBlock />);
                }
              });

              return comps;
            })()}
          </div>
        </Modal.Body>
        <Modal.Footer>
          <Button
            bsSize='large'
            bsStyle='primary'
            onClick={() => showModal(false)}
          >
            關閉
          </Button>
        </Modal.Footer>
      </Modal>
    );
  }
}
