// react
import React, { Component } from 'react';

// react-bootstrap
import {
  Button,
  Col,
  Image,
} from 'react-bootstrap';

// custom
import {
  skillImgs,
  namekoImgs,
} from '../lib/data';

export default class extends Component {
  render() {
    const { nameko, isSelected, onClick } = this.props;
    const { name, skills, usedSkills, values } = nameko;

    const className = 'nameko' + (isSelected ? ' selected' : '');

    return (
      <Col
        className={className}
        xs={4}
      >
        <Button
          onClick={() => onClick(nameko)}
        >
          <div
            className='img-ctn'
          >
            <Image
              className='img'
              src={namekoImgs[name]}
            />
          </div>
          <div>{name}</div>
          <div
            className='skill-ctn'
          >
            {skills.map((skill, idx) => (
              <div
                key={idx}
                style={{ backgroundImage: `url(${skillImgs[skill]})` }}
                className={'skill' + (usedSkills.indexOf(skill) === -1 ? '' : ' selected')}
              />
            ))}
          </div>
          <div>
            {values.map((value, idx) => (
              <span key={idx}>{value}</span>
            ))}
          </div>
        </Button>
      </Col>
    );
  }
}
