// react
import React, { Component } from 'react';

// react-bootstrap
import {
  Button,
  Glyphicon,
  Image,
  Modal,
} from 'react-bootstrap';

// custom
import {
  skillImgs,
  namekoImgs,
} from '../lib/data';

class SkillArea extends Component {
  render() {
    const { skills, usedSkills, onClick } = this.props;

    return (
      <div>
        {skills.map((skill, idx) => (
          <Button
            key={idx}
            className={'skill' + (usedSkills.indexOf(skill) === -1 ? '' : ' selected')}
            style={{ backgroundImage: `url(${skillImgs[skill]})` }}
            onClick={() => onClick('skill', skill)}
          />
        ))}
      </div>
    );
  }
}

class ValueArea extends Component {
  render() {
    const { values, onClick } = this.props;

    return (
      <div
        className='value-area'
      >
        {values.map((value, idx) => (
          <div
            key={idx}
          >
            <Button
              bsSize='large'
              onClick={() => onClick('add_value', idx)}
            >
              <Glyphicon glyph='triangle-top' />
            </Button>
            <span>{value}</span>
            <Button
              bsSize='large'
              onClick={() => onClick('sub_value', idx)}
            >
              <Glyphicon glyph='triangle-bottom' />
            </Button>
          </div>
        ))}
      </div>
    );
  }
}

export default class extends Component {
  render() {
    const { show, showModal, nameko, onClick } = this.props;

    if (!nameko) {
      return null;
    }

    const { name, skills, usedSkills, values } = nameko;

    return (
      <Modal
        className='nameko-modal'
        animation={false}
        show={show}
        onHide={() => showModal(false)}
      >
        <Modal.Header>
          <Modal.Title>編輯菇菇</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Image
            className='img'
            src={namekoImgs[name]}
            circle
          />
          <div>{name}</div>
          <SkillArea
            skills={skills}
            usedSkills={usedSkills}
            onClick={(type, value) => onClick(nameko, type, value)}
          />
          <ValueArea
            values={values}
            onClick={(type, value) => onClick(nameko, type, value)}
          />
        </Modal.Body>
        <Modal.Footer>
          <Button
            bsSize='large'
            bsStyle='primary'
            onClick={() => showModal(false)}
          >
            完成
          </Button>
        </Modal.Footer>
      </Modal>
    );
  }
}
